/* Titles for each section */
.enrichment-title {
  font-size: 2.5rem;
  color: #000000;
  text-transform: none;
}

.enrichment-container {
  padding: 2rem;
  text-align: center;
  background-color: #ffffff;
}

.enrichment-container section {
  margin-bottom: 5.2rem;
}

.class-list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap; /* Add wrap to allow items to wrap onto the next line */
  gap: 1rem;
}

.class-item {
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-sizing: border-box; /* Include padding and border in the width calculation */
  transition: transform 0.3s ease;
  text-align: center;
  max-width: 60%;
}

.class-item:hover {
  transform: scale(1.03);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.class-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.class-item h3 {
  color: #0055b8;
  font-size: 2rem;
  margin-top: 0.5rem;
}

.sign-up-btn {
  padding: 20px 15px;
  background-color: #66cc99;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1.25rem;
  cursor: pointer;
  text-align: center;
}

.sign-up-btn:hover,
.sign-up-btn:active {
  background-color: #93b6a4;
}

@media (max-width: 768px) {
  .class-list {
    flex-direction: column;
  }

  .class-item {
    max-width: 100%;
  }

  .class-item h3 {
    font-size: 1rem;
  }

  .class-item img {
    width: 100%;
    margin-bottom: 1rem;
  }
}
